body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
#container {
  width: 60%;
  margin: 20px auto;
}
.v-center {
  display: inline-block;
  vertical-align: middle;
  height: 100%;
  width: 0;
}
#container {
  display: inline-block;
  vertical-align: middle;
}
.book {
  -webkit-transform-style: preserve-3d;
          transform-style: preserve-3d;
  -webkit-transform: rotateX(45deg) rotateY(0deg) rotateZ(-45deg);
          transform: rotateX(45deg) rotateY(0deg) rotateZ(-45deg);
  -webkit-transition: -webkit-transform 1s;
  transition: -webkit-transform 1s;
  transition: transform 1s;
  transition: transform 1s, -webkit-transform 1s;
  position: absolute;
  left: 0;
  top: 0;
  right: 0;
  bottom: 15%;
  margin: auto;
  width: 30em;
  height: 40em;
}
.book .side {
  width: 3em;
  height: 40em;
  background: #b36060;
  position: absolute;
  left: -3em;
  top: 0;
  -webkit-transform-origin: 100% 100%;
          transform-origin: 100% 100%;
  -webkit-transform: rotateY(-90deg) rotateX(0deg);
          transform: rotateY(-90deg) rotateX(0deg);
}
.book .bottom {
  width: 25em;
  height: 3em;
  background: #e2e2e2;
  position: absolute;
  bottom: 0;
  left: 0;
  -webkit-transform-origin: 100% 100%;
          transform-origin: 100% 100%;
  -webkit-transform: rotateX(90deg);
          transform: rotateX(90deg);
}
.book.open {
  -webkit-transform: rotateX(35deg) rotateY(0deg) rotateZ(-35deg);
          transform: rotateX(35deg) rotateY(0deg) rotateZ(-35deg);
}
.book .paper,
.book .shadow {
  width: 25em;
  height: 40em;
  position: absolute;
  top: 0;
  left: 0;
}
.book .shadow {
  background: transparent;
  -webkit-transform: translateZ(-3em);
          transform: translateZ(-3em);
  box-shadow: -1em 1em 0px 0px #ccc3a9;
  z-index: 1;
}
.book .paper {
  -webkit-transition: box-shadow 0.5s 0.2s, -webkit-transform 1s;
  transition: box-shadow 0.5s 0.2s, -webkit-transform 1s;
  transition: transform 1s, box-shadow 0.5s 0.2s;
  transition: transform 1s, box-shadow 0.5s 0.2s, -webkit-transform 1s;
  -webkit-transform-origin: 0 50%;
          transform-origin: 0 50%;
  -webkit-transform-style: preserve-3d;
          transform-style: preserve-3d;
}
.book .paper.open {
  box-shadow: 2.2em 1em 0px 0px #ccc3a9;
  -webkit-transform: rotateX(0deg) rotateY(-180deg) rotateZ(0deg);
          transform: rotateX(0deg) rotateY(-180deg) rotateZ(0deg);
}
.book .paper .page.front {
  -webkit-transition: -webkit-transform 1s;
  transition: -webkit-transform 1s;
  transition: transform 1s;
  transition: transform 1s, -webkit-transform 1s;
  -webkit-transform-origin: 0 50%;
          transform-origin: 0 50%;
  -webkit-backface-visibility: hidden;
          backface-visibility: hidden;
  z-index: 2;
}

.book .page {
  width: 100%;
  height: 100%;
  position: absolute;
  -webkit-transform: translateZ(0px);
  transform: translateZ(0px);
}
.book .first .page {
  background: #ef9a9a;
}
.book .first .page.front:after {
  position: absolute;
  content: "";
  width: 50%;
  height: 100%;
  left: 0;
  top: 0;
  background: rgba(160, 115, 115, 0.1);
}
.book .paper .back {
  -webkit-transition: -webkit-transform 1s;
  transition: -webkit-transform 1s;
  transition: transform 1s;
  transition: transform 1s, -webkit-transform 1s;
  -webkit-transform-origin: 0 50%;
          transform-origin: 0 50%;
  background: #e1e1e1;
}
.book .first.paper .back {
  background: #ef9a9a;
}
.book .intro {
  position: absolute;
  width: 90%;
  width: calc(100% - 3em);
  height: 90%;
  height: calc(100% - 3em);
  border: 2em solid #eee;
  left: 0;
  top: 0;
  right: 0;
  bottom: 0;
  margin: auto;
}
.book .intro h2 {
  padding: 0.5em 0.5em;
  font-size: 2em;
  color: #fff;
  word-break: break-all;
  text-align: left;
  letter-spacing: 5px;
}
.book .intro h1 {
  padding: 0.5em 0.25em;
  font-size: 6em;
  color: #fff;
  word-break: break-all;
}
.book .paper:not(.first) .page {
  background: #f1f1f1;
  background-image: -webkit-linear-gradient(
      left,
      transparent 40px,
      #abced4 40px,
      #abced4 42px,
      transparent 42px
  ),
  -webkit-linear-gradient(#e1e1e1 0.1em, transparent 0.1em);
  background-image: linear-gradient(
      90deg,
      transparent 40px,
      #abced4 40px,
      #abced4 42px,
      transparent 42px
  ),
  linear-gradient(#e1e1e1 0.1em, transparent 0.1em);
  background-size: 100% 1.5em;
}
.book .paper:not(.first) .back {
  background: #e1e1e1;
}
.book .open.second .back {
  -webkit-transform: translateZ(-1px);
          transform: translateZ(-1px);
}
.book .open.third .back {
  -webkit-transform: translateZ(-2px);
          transform: translateZ(-2px);
}
.book .paper.first {
  z-index: 8;
}
.book .paper.second {
  z-index: 7;
}
.book .paper.third {
  z-index: 6;
}
.book .paper.fourth {
  z-index: 5;
}
.book .paper.first .front {
  -webkit-transform: translateZ(0.4px);
          transform: translateZ(0.4px);
}
.book .paper.second .front {
  -webkit-transform: translateZ(0.3px);
          transform: translateZ(0.3px);
}
.book .paper.third .front {
  -webkit-transform: translateZ(0.2px);
          transform: translateZ(0.2px);
}
.book .paper.fourth .front {
  -webkit-transform: translateZ(0.1px);
          transform: translateZ(0.1px);
}
.book .second-page #vara-container {
  width: 100%;
  height: 100%;
  padding: 1em;
}
#link {
  position: absolute;
  bottom: 10px;
  right: 10px;
  color: #615858;
  letter-spacing: 2px;
  text-decoration: none;
}
@media (max-width: 700px) {
  .book {
    left: 50%;
  }
}
html {
  height: 100%;
}
html body {
  margin: 0;
  padding: 0;
  height: 100%;
  font-size: 14px;
}
html body #root {
  height: 100%;
  width: 100%;
  font-family: Arial, 'Hiragino Sans GB', 'Microsoft Yahei', 'Microsoft Sans Serif', sans-serif;
}
html body #root .headerContent {
  height: 40px;
  line-height: 40px;
  padding: 0 20px;
  background: #0099FF;
}
html body #root .headerContent > a {
  margin-right: 20px;
  cursor: pointer;
  color: #fff;
  text-decoration: none;
}
html body #root .middleContent {
  height: calc(100% - 40px);
  overflow-y: auto;
}
html body #root .middleContent .articleContent {
  padding: 15px;
}
html body #root .middleContent .articleContent .articleLeft {
  position: fixed;
  width: 150px;
  height: 100%;
}
html body #root .middleContent .articleContent .articleLeft .searchContent {
  margin-bottom: 10px;
}
html body #root .middleContent .articleContent .articleLeft .typeContent > div {
  cursor: pointer;
  margin-bottom: 5px;
}
html body #root .middleContent .articleContent .articleLeft .typeContent > div:hover {
  color: #0099FF;
}
html body #root .middleContent .articleContent .articleLeft .pageContent {
  position: absolute;
  bottom: 50px;
  text-align: center;
  width: 100%;
}
html body #root .middleContent .articleContent .articleLeft .pageContent .ant-pagination-prev,
html body #root .middleContent .articleContent .articleLeft .pageContent .ant-pagination-next,
html body #root .middleContent .articleContent .articleLeft .pageContent .ant-pagination-jump-prev,
html body #root .middleContent .articleContent .articleLeft .pageContent .ant-pagination-jump-next {
  min-width: 24px;
}
html body #root .middleContent .articleContent .articleLeft .pageContent .ant-pagination-simple .ant-pagination-simple-pager input {
  padding: 0;
  margin-right: 0;
  width: 24px;
  color: #0099FF;
}
html body #root .middleContent .articleContent .articleLeft .pageContent .ant-pagination-slash {
  margin: 0 0 0 3px;
}
html body #root .middleContent .articleContent .articleRight {
  width: calc(100% - 170px);
  margin-left: 165px;
}
.viewContent .viewTitle {
  font-size: 16px;
  margin-bottom: 10px;
  font-weight: bold;
}
.viewContent .viewDetail code {
  display: block;
  overflow-x: auto;
  padding: 0.5em;
  background: #272822;
  color: #fff;
}

